  const userId = localStorage.getItem("user_id");
  const [markerFlag, setmarkerFlag] = useState(false);
  const [refresh, setRefresh] = useState(false);
  const [listingData, setListingData] = useState([]);
  const [addressError, setAddressError] = useState("");
  const [coordinates, setCoordinates] = useState({});
  const [currentLoc, setCurrentLoc] = useState(false);
  const [currentLocation, setCurrentLocation] = useState(null);
  const [showRadius, setShowRadius] = useState(false);
  const [checkedSalaryType, setCheckedSalaryType] = useState(
    new Set(["Hourly"])
  );
  const [fixMinSalary, setFixMinSalary] = useState(false);
  const [fixMaxSalary, setFixMaxSalary] = useState(false);
  const [hourlyMinSalary, setHourlyMinSalary] = useState(false);
  const [hourlyMaxSalary, setHourlyMaxSalary] = useState(false);
  const [erroralert, setErrorAlert] = useState(false);
  const [locationClear, setLocationClear] = useState(false);
  const [positionData, setPositionData] = useState([]);
  const [positionAll, setPositionAll] = useState([{}]);
  // const [tempCoord,setTempCoord]=useState(null)

  const [isChecked, setIsChecked] = useState(false);
  const cookies = new Cookies();
  let cookieLoc = cookies.get("locationHistory");
  const [locationHistory, setLocationHistory] = useState(
    cookieLoc ? cookieLoc : []
  );

  let cookiePos = cookies.get("positionHistory");
  const [positionHistory, setPositionHistory] = useState(
    cookiePos ? cookiePos : []
  );

  const [
    mapData,
    setMapData,
    sameLocationData,
    setSameLocationData,
    marker,
    setMarkerLocation,
    address,
    setAddress,
    filterValues,
    setFilterValues,
    homeCoordinates,
    setHomeCoordinates,
    zoomlvl,
    setZoomlvl,
    tempCoord,
    setTempCoord,
    tempPostion,
    setTempPosition,
    groupData,
    setGroupData,
    step,
    setStep,
    showModal,
    setShowModal,
    circles,
    setCircles,
    clusters,
    setClusters,
  ] = useContext(MapContext);
  const [flag, setFlag] = useState(false);
  const [locationFlag, setLocationFlag] = useState(false);
  const [loading, setLoading] = useState(true);
  const [positionClear, setPositionClear] = useState(tempPostion !== null);
  const history = useHistory();
  const { data: positionList } = useGetData("category-list");

  const { data: jobType } = useGetData("job-types");

  const selectPositionInputRef = useRef();

  useEffect(() => {
    setPositionData(positionList?.data);
  }, [positionList]);

  const latitude = localStorage.getItem("latitude");
  const longitude = localStorage.getItem("longitude");
  const [values, setValues] = useState({
    job_type: [],
    coordinates: homeCoordinates.coordinates,
    salary_type: "",
    is_remote: false,
  });

  const [autoZoomFalse, setAutoZoomFalse] = useState(true);

  useEffect(() => {
    setAutoZoomFalse(true);
  }, [values]);

  const { optional } = useParams();

  function toTitleCase(str) {
    if (str) {
      var wordsArray = str.toLowerCase().split(/\s+/);
      var upperCased = wordsArray.map(function (word) {
        return word.charAt(0).toUpperCase() + word.substr(1);
      });
      return upperCased.join(" ");
    }
  }

  const url_category = optional?.split("jobs")[0]?.replace(/-/g, " ")?.trim();

  const url_address = optional?.split("jobs-in-")[1]?.replace(/-/g, ", ");

  const routeChange = (add) => {
    if (!url_category && !filterValues?.position?.name) {
      let path = `/jobsearch/jobs-in-${add
        .replace(/,\s+/g, "-")
        .replace(/\s+/g, "-")}`;
      if (optional) {
        history.push(path);
      }
      history.push(path);
    } else {
      let path = `/jobsearch/${
        filterValues?.position?.name
          ? filterValues?.position?.name.replace(/\s+/g, "-").replace(" ", "")
          : url_category.replace(/\s+/g, "-").replace(" ", "")
      }-jobs-in-${add.replace(/,\s+/g, "-").replace(/\s+/g, "-")}`;
      if (optional) {
        history.push(path);
      }
      history.push(path);
    }
  };

  const [countApiValues, setCountApiValues] = useState({
    job_type: [],
    coordinates: homeCoordinates.coordinates,
    step:
      zoomlvl == 4
        ? 2.5
        : zoomlvl == 5
        ? 1.5
        : zoomlvl == 6
        ? 0.8
        : zoomlvl == 7
        ? 0.4
        : null,
  });
  const [data, setData] = useState(null);
  const [isJobsApi, setIsJobsApi] = useState(true);

  //Access control API call
  const [accessUrl, setAccessUrl] = useState(`access/1009`);
  const { data: AccessData, loading: filterLoad } = useGetData(accessUrl);

  //for Parallax effect checking if mobile
  const [width, setWidth] = useState(window.innerWidth);

  function handleWindowSizeChange() {
    setWidth(window.innerWidth);
  }
  useEffect(() => {
    window.addEventListener("resize", handleWindowSizeChange);
    return () => {
      window.removeEventListener("resize", handleWindowSizeChange);
    };
  }, []);

  const isMobile = width <= 767;

  const [filter, setFilter] = useState(!isMobile);

  function featureFilter() {
    let sample = false;
    AccessData?.data?.map((single_access_item) => {
      if (single_access_item.feature === 3 && userId) {
        sample = true;
      }
    });
    return sample;
  }

  //check queries in url
  function useQuery() {
    return new URLSearchParams(useLocation().search);
  }

  let query = useQuery();

  useEffect(() => {
    if (tempCoord) {
      setMarkerLocation((currentData) => ({
        ...currentData,
        lat: tempCoord.lat,
        lng: tempCoord.lan,
      }));
      setTempCoord(null);
    }
    if (url_category) {
      setPositionClear(true);
      setTempPosition({ id: 1, name: url_category });
    }
    if (url_address) {
      setLocationClear(true);
    }
  }, []);
  //check for position query in url
  useEffect(() => {
    // if (!userId) {
    //   window.location.href = "/login";
    // }

    if (url_address) {
      setAddress(url_address);
      handleSelect(url_address);
    } else if (query.get("location")) {
      setAddress(query.get("location"));
    }

    if (query.get("position")) {
      const positionStr = query.get("position").replace(/_/g, " ");
      setFilterValues((prev) => ({
        ...prev,
        position: { id: null, name: positionStr },
      }));
    }
    setmarkerFlag(true);

    return () => {
      setFilterValues((prev) => ({
        ...prev,
        position: null,
        min: 1,
        max: 50,
        jobType: [],
        radius: 0,
        salary: false,
        salary_type: "Hourly",
        is_remote: false,
      }));
    };
  }, []);

  useEffect(() => {
    if (homeCoordinates?.coordinates) {
      setFlag(true);
    }

    if (homeCoordinates?.position) {
      setFilterValues({
        ...filterValues,
        position: homeCoordinates.position,
      });
    }
  }, []);

  // set full time as default
  // useEffect(() => {
  //   if (jobType) {
  //     const types = jobType?.data?.response_data;
  //     const filteredType = types.filter((type) => type.name === "Full Time");

  //     if (filteredType?.length > 0) {
  //       setFilterValues({
  //         ...filterValues,
  //         jobType: [filteredType[0].id],
  //       });
  //     }
  //   }
  // }, [jobType]);

  let token = localStorage.getItem("accessToken");
  let headerData = {
    "Content-Type": "application/json",
  };

  if (token) {
    headerData.Authorization = `Token ${token}`;
  }

  useEffect(() => {
    if (Object.keys(homeCoordinates).length !== 0) {
      let token = localStorage.getItem("accessToken");

      setMarkerLocation((currentData) => ({
        ...currentData,
        lat: marker.lat,
        lng: marker.lng,
      }));
      setCoordinates(homeCoordinates);

      const sendData = {
        coordinates: homeCoordinates.coordinates,
        position_name: homeCoordinates.position_name,
      };
      let url;
      if (zoomlvl <= 7) {
        sendData.job_type = [];
        sendData.step =
          zoomlvl == 4
            ? 2.5
            : zoomlvl == 5
            ? 1.5
            : zoomlvl == 6
            ? 0.8
            : zoomlvl == 7
            ? 0.4
            : null;
        url = `${process.env.REACT_APP_SEARCH_URL}/jobs/summary`;
        setIsJobsApi(false);
      } else {
        let jobsApi = true;
        return axios
          .post(`${process.env.REACT_APP_SEARCH_URL}/alljobs/count`, sendData, {
            headers: headerData,
          })
          .then((res) => {
            if (res?.data?.total_count > 1000 || zoomlvl <= 7) {
              sendData.step = zoomlvl < 10 ? 0.2 : 0.1;
              setIsJobsApi(false);
              jobsApi = false;
              url = `${process.env.REACT_APP_SEARCH_URL}/jobs/summary`;
            } else {
              setIsJobsApi(true);
              url = `${process.env.REACT_APP_SEARCH_URL}/jobs`;
            }

            axios
              .post(url, sendData, {
                headers: headerData,
              })
              .then((res) => {
                if (res?.data && jobsApi) {
                  let jobs = [...res?.data];
                  //sorting premium jobs
                  jobs.sort((a, b) => b.is_premium - a.is_premium);
                  setGroupData([]);
                  // setSameLocationData(obj);
                  setListingData(jobs);
                  setLoading(false);
                  // for clustering replacing lng for longitude and lat for latitude
                  jobs = jobs.map(
                    ({ longtitude: lng, latitude: lat, ...rest }) => ({
                      lng,
                      lat,
                      ...rest,
                    })
                  );
                  setMapData(jobs);
                  setFlag(false);
                } else if (res?.data && !jobsApi) {
                  setSameLocationData([]);
                  setGroupData(res.data?.data);
                  setMapData([]);
                  setLoading(false);
                  setFlag(false);
                }
              })
              .catch((err) => {
                console.log("err :>> ", err);
              });
          });
      }
      console.log("sendData :>> ", sendData);
      axios
        .post(url, sendData, {
          headers: headerData,
        })
        .then((res) => {
          if (res.data && zoomlvl > 7) {
            setFlag(false);
            let jobs = [...res?.data];

            // //Add data with same locations in arr
            // let arr = [];

            // for (let i = 0; i < jobs.length; i++) {
            //   for (let j = i + 1; j < jobs.length; j++) {
            //     if (
            //       jobs[i].latitude === jobs[j].latitude &&
            //       jobs[i].longtitude === jobs[j].longtitude
            //     ) {
            //       !arr.includes(jobs[i]) && arr.push(jobs[i]);
            //       !arr.includes(jobs[j]) && arr.push(jobs[j]);
            //     }
            //   }
            // }

            // //seperate same locations into different arrays
            // let obj = {};
            // let jobData = res.data;
            // for (let i = 0; i < jobData.length; i++) {
            //   obj[i] = [];
            //   for (let j = i + 1; j < jobData.length; j++) {
            //     if (
            //       jobData[i].latitude === jobData[j].latitude &&
            //       jobData[i].longtitude === jobData[j].longtitude
            //     ) {
            //       if (!obj[i].includes(jobData[i])) {
            //         obj[i].push(jobData[i]);
            //       }

            //       obj[i].push(jobData[j]);
            //       jobData.splice(j, 1);
            //       j = j - 1;
            //     }
            //   }
            // }

            // // deleting empty arrays in object
            // Object.keys(obj).forEach((i) => {
            //   if (obj[i].length === 0) {
            //     delete obj[i];
            //   } else {
            //     obj[i].sort((a, b) => b.is_premium - a.is_premium);
            //   }
            // });

            // //group same location data by company_name
            // Object.keys(obj).forEach((i) => {
            //   let key = "company_name";

            //   obj[i].modal_data = obj[i].reduce((hash, obj) => {
            //     if (obj[key] === undefined) return hash;
            //     return Object.assign(hash, {
            //       [obj[key]]: (hash[obj[key]] || []).concat(obj),
            //     });
            //   }, {});
            // });

            // //Remove same location data from other datas
            // const filteredJobData = jobs.filter(
            //   (i) => !arr.find((f) => f.id === i.id)
            // );

            //sorting premium jobs
            jobs.sort((a, b) => b.is_premium - a.is_premium);
            // setSameLocationData(obj);
            setListingData(jobs);

            // for clustering replacing lng for longitude and lat for latitude
            jobs = jobs.map(({ longtitude: lng, latitude: lat, ...rest }) => ({
              lng,
              lat,
              ...rest,
            }));

            setMapData(jobs);
            setGroupData([]);
            setLoading(false);
          } else if (res?.data && zoomlvl <= 7) {
            setSameLocationData([]);
            setGroupData(res.data?.data);
            setMapData([]);
            setLoading(false);
            setFlag(false);
          }
        })
        .catch((err) => {
          console.log("err :>> ", err);
        });
    }
  }, []);

  const handleGeolocationMarker = (lat, lng) => {
    if (!homeCoordinates || Object.keys(homeCoordinates).length === 0) {
      setMarkerLocation((currentData) => ({
        ...currentData,
        lat: lat,
        lng: lng,
      }));
    }
  };

  const updateSalary = (value) => {
    setFilterValues({ ...filterValues, min: value[0], max: value[1] });
  };
  console.log(filterValues, "data...");

  //Remote function
  const updateRemote = (e) => {
    setFilterValues({
      ...filterValues,
      is_remote: e.target.checked ? true : false,
    });
    setValues((prev) => ({
      ...prev,
      coordinates: coordinates.coordinates,
      is_remote: e.target.checked ? true : false,
    }));
    setCountApiValues((current) => {
      const { salary, ...rest } = current;
      return rest;
    });
  };

  const updateSalaryFilter = () => {
    setValues({
      ...values,
      coordinates: coordinates.coordinates,
      salary: {
        min: filterValues.min,
        max: filterValues.max,
      },
      salary_type: filterValues.salary_type,
      is_remote: filterValues.is_remote,
    });
    setCountApiValues({
      ...countApiValues,
      coordinates: coordinates.coordinates,
      salary: {
        min: filterValues.min,
        max: filterValues.max,
      },
    });
  };

  const updateType = (e, id) => {
    const check = e.target.checked;
    if (check) {
      setFilterValues({
        ...filterValues,
        jobType: [...filterValues.jobType, id],
      });
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
        job_type: [...values.job_type, id],
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        job_type: [...values.job_type, id],
      });
    } else {
      const filterData = filterValues.jobType.filter((item) => {
        return item !== id;
      });
      setFilterValues({
        ...filterValues,
        jobType: filterData,
      });
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
        job_type: filterData,
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        job_type: filterData,
      });
    }
  };

  const updateRadius = (value) => {
    setFilterValues({ ...filterValues, radius: value });
    setShowRadius(true);
  };

  const updateRadiusFilter = (value) => {
    setTimeout(() => {
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
        radius: value,
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        radius: value,
      });
      setRefresh(!refresh);
    }, 1000);
  };

  const updateSalaryShow = (e) => {
    if (!e.target.checked) {
      // checkedSalaryType.clear();
    } else {
      checkedSalaryType.clear();
      checkedSalaryType.add("Hourly");
    }
    setFilterValues({
      ...filterValues,
      salary: !filterValues.salary,
      salary_type: "Hourly",
    });
    setValues((prev) => ({
      ...prev,
      coordinates: coordinates.coordinates,
      salary_type: e.target.checked ? "Hourly" : null,
      salary: e.target.checked
        ? {
            min: filterValues.min,
            max: filterValues.max,
          }
        : null,
    }));
    setCountApiValues((current) => {
      const { salary, ...rest } = current;
      return rest;
    });
    // setValues({
    //   ...values,
    //   salary_type:"Hourly"
    // })
  };

  const calculateAnnualPay = useCallback((pay) => {
    let yearlyPay = pay * 261 * 8;
    return yearlyPay;
  }, []);

  useEffect(() => {
    if (tempPostion) {
      setFilterValues({ ...filterValues, position: tempPostion });
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
        position_name: tempPostion.name,
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        position_name: tempPostion.name,
      });
    }
    if (url_category) {
      setFilterValues({
        ...filterValues,
        position: { id: 1, name: url_category },
      });
      setValues({
        ...values,
        position_name: url_category,
      });
      setCountApiValues({
        ...countApiValues,
        position_name: url_category,
      });
    }
  }, []);

  const updatePositionSelectValue = (value) => {
    if (value) {
      let filtered = positionData.filter((ele) => {
        return ele.name != value.name;
      });
      setPositionData(filtered);
    }
    if (value) {
      if (
        !positionHistory.some((ele) => ele.name === value.name) &&
        positionHistory.length !== 3
      ) {
        setPositionHistory((oldArray) => [value, ...oldArray]);
      } else if (
        !positionHistory.some((ele) => ele.name === value.name) &&
        positionHistory.length === 3
      ) {
        let temp = positionHistory.pop();
        if (!positionData.some((ele) => ele.name === temp.name)) {
          setPositionData((oldArray) => [temp, ...oldArray]);
        }
        setPositionHistory((oldArray) => [value, ...oldArray]);
      } else if (positionHistory.some((ele) => ele.name === value.name)) {
        let filtered = positionHistory.filter((ele) => {
          return ele.name != value.name;
        });
        setPositionHistory(filtered);
        setPositionHistory((oldArray) => [value, ...oldArray]);
      }
    }

    if (address && value) {
      let path = `/jobsearch/${value.name.replace(
        /\s+/g,
        "-"
      )}-jobs-in-${address.replace(/,\s+/g, "-").replace(/\s+/g, "-")}`;
      history.push(path);
    } else if (address && !value) {
      let path = `/jobsearch/jobs-in-${address
        .replace(/,\s+/g, "-")
        .replace(/\s+/g, "-")}`;
      history.push(path);
    } else if (!value) {
      let path = `/jobsearch`;
      history.push(path);
    } else {
      let path = `/jobsearch/${value.name.replace(/\s+/g, "-")}-jobs`;
      history.push(path);
    }
    // routeChange()
    setTempPosition(value);
    setFilterValues({ ...filterValues, position: value });
    if (value) {
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
        position_name: value.name,
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        position_name: value.name,
      });
    } else {
      setValues({
        ...values,
        coordinates: coordinates.coordinates,
      });
      setCountApiValues({
        ...countApiValues,
        coordinates: coordinates.coordinates,
        position_name: [],
      });
    }
    if (value) setPositionClear(true);
  };

  const updateListing = (arr) => {
    if (arr) {
      //sorting premium jobs
      arr.sort((a, b) => b.is_premium - a.is_premium);
      setListingData(arr);
      setLoading(false);
    }
  };

  const handleChange = (address) => {
    setAddress(address);
    setLocationClear(true);
    if (locationFlag) {
      setLocationFlag(false);
    }
  };

  useEffect(() => {
    let json_str = JSON.stringify(locationHistory);
    cookies.set("locationHistory", json_str);
  }, [locationHistory]);

  useEffect(() => {
    let json_str = JSON.stringify(positionHistory);
    cookies.set("positionHistory", json_str);
  }, [positionHistory]);
  const handleClickLoc = (address) => {
    setAutoZoomFalse(true);
    setAddress(address);
    setLocationClear(true);
    geocodeByAddress(address)
      .then((results) => {
        return getLatLng(results[0]);
      })
      .then((latLng) => getSelectedLocation(latLng))
      .catch((error) => console.error("Error", error));
  };

  const removeLocationHistory = (address) => {
    setLocationHistory(locationHistory.filter((item) => item !== address));
  };

  const handleSelect = (address) => {
    setAutoZoomFalse(true);
    if (!locationHistory.includes(address) && locationHistory.length < 5) {
      setLocationHistory((oldArray) => [address, ...oldArray]);
    } else if (
      !locationHistory.includes(address) &&
      locationHistory.length === 5
    ) {
      locationHistory.pop();
      setLocationHistory(locationHistory);
      setLocationHistory((oldArray) => [address, ...oldArray]);
    } else if (locationHistory.includes(address)) {
      const index = locationHistory.indexOf(address);
      if (index > -1) {
        locationHistory.splice(index, 1);
      }
      setLocationHistory(locationHistory);
      setLocationHistory((oldArray) => [address, ...oldArray]);
    }
    routeChange(address);
    setAddress(address);
    setLocationFlag(true);
    geocodeByAddress(address)
      .then((results) => {
        return getLatLng(results[0]);
      })
      .then((latLng) => getSelectedLocation(latLng))
      .catch((error) => console.error("Error", error));
  };

  const getSelectedLocation = (location) => {
    setMarkerLocation((currentData) => ({
      ...currentData,
      lat: location.lat,
      lng: location.lng,
    }));
  };
  // useEffect(()=>{
  //   let cleanTimeout = setTimeout(()=>{
  //     console.log('API called');
  //   },2000);

  //   return ()=>{
  //     console.log('clean up function');
  //     clearTimeout(cleanTimeout);
  //   }

  // },[values]);

  let apiTimeout;

